<template>
  <div id="app">
    <!--  -->
    <div v-if="!$route.meta.hideSidebar" class="sidebar">
      <div class="sidebar-header">
        <!--  +  + + -->
        <van-image round width="32px" height="32px" :src="require('./assets/images/my_profile.jpg')" alt=""
          @click="handleSidebarAvatarClick" class="avatar-hover" />
        <!-- localStorage -->
        <span class="username">{{ username }}</span>

        <!-- +juli -->
        <van-popover v-model:show="showPopover" :actions="actions" placement="bottom-end" :offset="[13, 8]"
          @select="handleActionSelect">
          <template #reference>
            <van-icon name="plus" color="white" size="15" class="icon-hover" />
          </template>
        </van-popover>
      </div>

      <div class="sidebar-menu">
        <!--  -->
        <div class="menu-group">
          <!--  -->
          <div class="menu-item" :class="{ 'active': active === 'message' }" @click="toggleMessageDropdown">
            <van-icon name="chat-o" class="menu-icon" />
            <span class="menu-text"></span>
            <!--  -->
            <van-icon :name="messageDropdownOpen ? 'arrow-up' : 'arrow-down'" class="dropdown-arrow" />
          </div>

          <!--  -->
          <div class="dropdown-wrapper">
            <transition name="dropdown-fade">
              <div class="message-dropdown" v-if="messageDropdownOpen">
                <div class="message-dropdown-content">
                  <ul class="message-list">
                    <li v-for="(item, index) in friendMessageList" :key="item.name" class="message-item"
                      @click="handleMessageItemClick(item)">
                      <van-image round width="40px" height="40px" :src="item.profile" class="message-avatar" />
                      <span class="message-name">{{ item.name }}</span>
                    </li>
                  </ul>
                </div>
              </div>
            </transition>
          </div>
          <div class="menu-item" :class="{ 'active': active === 'emotion' }" @click="navigateTo('/emotion', 'emotion')">
          <!--  -->
          <svg class="menu-icon" width="24" height="24" viewBox="0 0 24 24" fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <!--  -->
            <path d="M4 18L10 15L4 12V18Z" stroke="currentColor" stroke-width="2" stroke-linecap="round"
              stroke-linejoin="round" />
            <path d="M10 15H20V9H10V15Z" stroke="currentColor" stroke-width="2" stroke-linecap="round"
              stroke-linejoin="round" />
            <!--  -->
            <circle cx="17" cy="7" r="1.5" fill="currentColor" />
          </svg>
          <span class="menu-text"></span>
        </div>
        </div>
        <div class="menu-item" :class="{ 'active': active === 'friends' }"
          @click="navigateTo('/home/friends', 'friends')">
          <van-icon name="friends-o" class="menu-icon" />
          <span class="menu-text">juli</span>
        </div>
        <div class="menu-item" :class="{ 'active': active === 'discover' }"
          @click="navigateTo('/home/discover', 'discover')">
          <van-icon name="underway-o" class="menu-icon" />
          <span class="menu-text"></span>
        </div>
        

      </div>
    </div>

    <!--  -->
    <div class="main-container">
      <!--  -->
      <div class="content-area">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      messageDropdownOpen: false,  // /
      // 
      friendMessageList: [
        {
          name: "",
          profile: require('./assets/images/profile_.jpg')
        },
        {
          name: "",
          profile: require('./assets/images/profile_02.jpg')
        },
        {
          name: "",
          profile: require('./assets/images/profile_.jpg')
        }
      ],
      active: 'message',
      titleMap: {
        message: '',
        friends: 'juli',
        discover: '',
        emotion: ''
      },
      showPopover: false,
      actions: [
        { text: '1', icon: 'gift-o' },
      ],
      username: '' // localStorage
    }
  },
  computed: {
    currentTitle() {
      return this.titleMap[this.active] || ''
    }
  },
  methods: {
    // 
    toggleMessageDropdown() {
      this.messageDropdownOpen = !this.messageDropdownOpen;
      // 
      this.active = 'message';
    },

    // 
    handleMessageItemClick(item) {
      // 
      const index = item.name.indexOf('');
      const career = index !== -1 ? item.name.substring(0, index) : item.name;
      localStorage.setItem('career', career);

      // discover1
      if (item.name === "") {
        this.$router.push({
          path: '/home/discover1',
          query: {
            interviewer: item.name
          }
        });
      }
      // action
      else if (item.name === "") {
        this.$router.push({
          path: '/action',
          query: {
            interviewer: item.name
          }
        });
      }
      // 
      else {
        this.$router.push(`/dialog/${item.name}`);
      }

      // 
      this.messageDropdownOpen = false;
    },
    navigateTo(path, name) {
      this.$router.push(path)
      this.active = name
    },
    handleLeftClick() {
      this.$router.push('/side')
    },
    handleTopAvatarClick() {
      this.$router.push('/side')
    },
    handleSidebarAvatarClick() {
      this.$router.push('/side')
    },
    handleActionSelect(action) {
      if (action.text === 'juli') {
        window.location.href = 'https://123.56.203.202/ui'
      }
      this.showPopover = false
    },
    // localStorage
    loadUsernameFromLocalStorage() {
      try {
        const userInfo = localStorage.getItem('currentUser')
        if (userInfo) {
          const parsedUser = JSON.parse(userInfo)
          this.username = parsedUser.username || ''
        } else {
          this.username = ''
        }
      } catch (error) {
        console.error('localStorage:', error)
        this.username = ''
      }
    }
  },
  watch: {
    '$router.path'(newPath) {
      const pathMap = {
        '/home/message': 'message',
        '/home/friends': 'friends',
        '/home/discover': 'discover',
        '/emotion': 'emotion'
      }
      this.active = pathMap[newPath] || 'message'
    }
  },
  mounted() {
    const pathMap = {
      '/home/message': 'message',
      '/home/friends': 'friends',
      '/home/discover': 'discover',
      '/emotion': 'emotion'
    }
    this.active = pathMap[this.$route.path] || 'message'
    this.loadUsernameFromLocalStorage()
  }
}
</script>

