<template>
  <div class="nav-container">
    <!--  -->
    <div v-if="isLoading" class="loading">
      <p>...</p>
    </div>

    <!--  -->
    <div v-if="errorMessage" class="error">
      <p> {{ errorMessage }}</p>
    </div>

    <!--  -->
    <div v-if="!isLoading && !errorMessage && sortedBlogs.length === 0" class="no-data">
      <p></p>
    </div>

    <!--  -->
    <div class="blog-container" v-if="!isLoading && !errorMessage && sortedBlogs.length > 0">
      <el-card v-for="blog in sortedBlogs" :key="blog.date" @click="openBlogModal(blog)" class="blog-card">
        <template #header>
          <h3>{{ blog.title }}</h3>
          <h4>{{ blog.username }}</h4>
          <span class="blog-date">{{ formatDate(blog.date) }}</span>
          <p class="comment-count">{{ getCommentsForBlog(blog.date).length }}</p>
        </template>
        <!--  -->
        <div class="blog-content">
          {{ decodedContent(blog.content).length > 150 
            ? decodedContent(blog.content).slice(0, 150) + '... ' 
            : decodedContent(blog.content) 
          }}
        </div>
      </el-card>
    </div>

    <!--  -->
    <div v-if="showBlogModal" class="modal-overlay" @click="closeBlogModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h2>{{ currentBlog.title }}</h2>
          <button class="close-btn" @click="closeBlogModal"></button>
        </div>
        <div class="modal-body">
          <div class="blog-meta">
            <span class="author">{{ currentBlog.username }}</span>
            <span class="date">{{ formatDate(currentBlog.date) }}</span>
          </div>
          <!--  Markdown -->
          <!--  -->
<div 
  class="full-content markdown-content"  
  v-html="renderMarkdown(decodedContent(currentBlog.content))"
></div>
        </div>
        <div class="modal-footer">
          <button class="close-modal-btn" @click="closeBlogModal"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import { ElCard } from 'element-plus';
//  Markdown npm install marked
import { marked } from 'marked';

// 
const sortedBlogs = ref([]);
const allComments = ref([]);
const isLoading = ref(true);      // 
const errorMessage = ref('');     // 
const showBlogModal = ref(false); // 
const currentBlog = ref({});      // 

// 
const formatDate = (dateString) => {
  try {
    return new Date(dateString).toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  } catch (error) {
    console.error(':', error);
    return dateString; // 
  }
};

// 
const getCommentsForBlog = computed(() => (blogDate) => {
  if (!allComments.value) return [];
  return allComments.value.filter(comment => comment && comment.blogDate === blogDate);
});

//  Unicode + 
const decodedContent = (content) => {
  if (!content) return '';
  // 1.  Unicode \uXXXX
  let decoded = content.replace(/\\u([0-9a-fA-F]{4})/g, (match, hex) => {
    return String.fromCharCode(parseInt(hex, 16));
  });
  // 2.  HTML  &lt; &gt; 
  decoded = decoded
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&');
  // 3.  \r\n  \n
  decoded = decoded.replace(/\r\n/g, '\n').replace(/\r/g, '\n');
  return decoded;
};

// 
const fetchBlogs = async () => {
  try {
    isLoading.value = true;
    errorMessage.value = '';

    console.log('...');
    const [blogsRes, commentsRes] = await Promise.all([
      axios.get('https://123.56.203.202/getBlogs'),
      axios.get('https://123.56.203.202/getComments')
    ]);

    console.log(':', blogsRes.data);
    console.log(':', commentsRes.data);

    // content
    let blogsData = Array.isArray(blogsRes.data) ? blogsRes.data : [];
    
    // 
    const targetPatterns = [
      /^```markdownrn#/,  // ```markdownrn#
      /^# u592/           // # u592
    ];
    
    // fetchBlogs
blogsData = blogsData.map((blog, index) => {
  if (blog.content) {
    // uXXXX
    const hasUnicode = /\\u([0-9a-fA-F]{4})/.test(blog.content) || /u([0-9a-fA-F]{4})/.test(blog.content);
    const hasHtmlEntities = /&lt;|&gt;|&amp;/.test(blog.content);
    const hasLineBreaks = /\r\n|\r/.test(blog.content);
    
    const encodingTypes = [];
    if (hasUnicode) encodingTypes.push('UnicodeuXXXX\\uXXXX');
    if (hasHtmlEntities) encodingTypes.push('HTML&lt;');
    if (hasLineBreaks) encodingTypes.push('\\r\\n');
    
    // ```markdownrn## u592
    const isTargetBlog = /^```markdownrn#/.test(blog.content) || /^# u592/.test(blog.content);
    
    if (isTargetBlog) {
      console.group(`[${index}] - : ${encodingTypes.length ? encodingTypes.join('') : ''}`);
      console.log(':', blog.content);
    }
    
    // uXXXX
    let decodedContent = blog.content
      // Unicode\uXXXX
      .replace(/\\u([0-9a-fA-F]{4})/g, (match, hex) => String.fromCharCode(parseInt(hex, 16)))
      // UnicodeuXXXX\
      .replace(/u([0-9a-fA-F]{4})/g, (match, hex) => String.fromCharCode(parseInt(hex, 16)))
      // HTML
      .replace(/&lt;/g, '<')
      .replace(/&gt;/g, '>')
      .replace(/&amp;/g, '&')
      // 
      .replace(/\r\n/g, '\n')
      .replace(/\r/g, '\n');
    
    if (isTargetBlog) {
      console.log(':', decodedContent);
      console.groupEnd();
    }
    
    return { ...blog, content: decodedContent };
  }
  return blog;
});

    // 
    sortedBlogs.value = blogsData;
    allComments.value = Array.isArray(commentsRes.data) ? commentsRes.data : [];

  } catch (error) {
    console.error(':', error);
    errorMessage.value = '';
    if (error.response) {
      console.error(':', error.response.status, error.response.data);
    } else if (error.request) {
      console.error(':', error.request);
    }
  } finally {
    isLoading.value = false;
  }
};


// 
const openBlogModal = (blog) => {
  const decoded = decodedContent(blog.content); // 
  currentBlog.value = { ...blog, decodedContent: decoded }; // 
  showBlogModal.value = true;
  document.body.style.overflow = 'hidden';
};
// 
const closeBlogModal = () => {
  showBlogModal.value = false;
  document.body.style.overflow = ''; // 
  currentBlog.value = {};
};

const renderMarkdown = (content) => {
  if (!content) return '<p></p>';
  try {
    // 1. 
    let processed = content.replace(/RADAR_DATA_.*?END/g, '');
    // 2.  Markdown 
    processed = processed.replace(/\n/g, '  \n');
    // 3.  Markdown
    return marked.parse(processed);
  } catch (error) {
    console.error('Markdown:', error);
    return `<p>${content}</p>`;
  }
};

// 
onMounted(async () => {
  await fetchBlogs();
});
</script>

